.component {
    background-color: white;
    width: 268px;
    height: 178px;
    padding-bottom: 2em;
    border: 0.1px solid black;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
  }
  
  .musicCover {
    height: 50px;
    width:70px;
    border-radius: 10%;
  }
  
  .playButton {
    background: none;
    border: none;
    align-items: center;
    justify-content: center;
  }
  
  .subTitle {
    margin-top: -1em;
    color: #4f4f4f;
  }
  
  .time {
    margin: 0 auto;
    width: 215px;
    display: flex;
    justify-content: space-between;
    color: #828282;
    font-size: smaller;
  }
  
  .timeline {
    width: 80%;
    background-color: #27ae60;
  }
  
  input[type="range"] {
    background-color: #27ae60;
  }
  
  @media (max-width: 900px) {
    .component {
      width: 50%;
    }
  }